.coin-container {
    display: flex;
    justify-content: center;
    background-color: #f8f0f6; 
}

.coin-heading {
    display: flex;
    justify-content: center;
}

.coin-input {
    margin: 10px;
    padding: 5px;
    border-radius: 5px;
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px; 
    border-bottom: 1px solid #2b0488;
    width: 80%;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;
}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 900px;
}


.coin-price {
    width: 130px;
    padding: 10px;
     Padding-right:85px; 
      
}

.coin-volume {
    width: 155px;
    padding: 10px;
}

.coin-marketcap {
    width: 230px;
}

.coin-percent {
    width: 100%;
     padding: 10px; 
    /* padding-left: -20px; */
}

.red {
    color: rgb(202, 7, 7);
}

.green {
    color: rgb(5, 171, 5);
}